import styled from "styled-components";
import logo from "../Data/Sahil_Khan.png";

const About = () => {
  return (
    <AboutDiv id="about">
      <Description>
        <Heading>About Me</Heading>
        <Title>I'm Sahil Khan, a MERN stack developer.</Title>
        <Para>
          An enthusiastic and passionate developer dedicated to building and
          optimizing user-focused web applications. Curious about learning new
          tech stacks and looking forward to starting a career as a competent
          web developer.
        </Para>
      </Description>
      <Image src={logo} />
    </AboutDiv>
  );
};

export default About;

const AboutDiv = styled.div`
  height: 600px;
  display: grid;
  grid-template-columns: repeat(2, 60% 40%);
  gap: 20px;
`;

const Description = styled.div`
  /* border: 2px solid orange; */
  margin-left: 150px;
  margin-top: 50px;
  text-align: center;
`;

const Image = styled.img`
  width: 300px;
  height: 400px;
  border-radius: 15px;
  margin: 100px 0px 0px 100px;
`;

const Heading = styled.h1`
  color: #149eca;
  font-family: "Courier New", Courier, monospace;
  text-decoration: underline;
  font-size: 40px;
`;

const Title = styled.h1`
  color: #76c8e4;
  text-align: left;
  margin-top: 80px;
  font-family: Arial, Helvetica, sans-serif;
`;

const Para = styled.h3`
  font-family: "Courier New", Courier, monospace;
  color: white;
  font-size: 22px;
  line-height: 30px;
  /* margin-top: 100px; */
  text-align: left;
`;
