import styled from "styled-components";
// import logo from "../Data/new.png";
import "./home.css";

const About = () => {
  const redirect = () => {
    console.log(window);
    window.open(
      "https://drive.google.com/file/d/1eWRAqunjozqzfwnEGNeomWvII9eFjcqw/view?usp=sharing"
    );
  };
  return (
    <HomeDiv id="home">
      <Container>
        <Image src={require("../../Data/programmer.svg").default} />
        <Heading>
          <h1>Hello, I'm a</h1>
          <h1>Full-Stack Developer</h1>
          <button className="resume-btn" onClick={redirect}>
            My Resume
          </button>
        </Heading>
      </Container>
    </HomeDiv>
  );
};

export default About;

const HomeDiv = styled.div`
  height: 600px;
  /* width: 100%; */
`;

const Container = styled.div`
  /* width: 100%; */
  display: flex;
`;

const Image = styled.img`
  width: 500px;
  margin: 90px 0px 0px 100px;
`;

const Heading = styled.div`
  font-size: 24px;
  margin: 100px 0px 0px 150px;
  color: white;
  font-family: "Courier New", Courier, monospace;
`;

