import React from "react";
import styled from "styled-components";

const SKill = () => {
  return (
    <SkillDiv id="skill">
      <Heading>What I do</Heading>
      <ImageDiv>
        <Image src={require("../Data/html.svg").default} alt="html-icon" />
        <Image src={require("../Data/css.svg").default} alt="css-icon" />
        <Image
          src={require("../Data/javascript.svg").default}
          alt="javascript-icon"
        />
        <Image
          src={require("../Data/mongodb.svg").default}
          alt="mongodb-icon"
        />
        <Image
          src={require("../Data/expressjs.svg").default}
          alt="expressjs-icon"
        />
        <Image src={require("../Data/react.svg").default} alt="react-icon" />
        <Image src={require("../Data/nodejs.svg").default} alt="nodejs-icon" />
        <Image src={require("../Data/git.svg").default} alt="git-icon" />
        <Image src={require("../Data/npm.svg").default} alt="npm-icon" />
        <Image src={require("../Data/redux.svg").default} alt="redux-icon" />
        <Image
          src={require("../Data/typescript.svg").default}
          alt="typescript-icon"
        />
        <Image
          src={require("../Data/chakra-ui.svg").default}
          alt="chakra-ui-icon"
        />
      </ImageDiv>
    </SkillDiv>
  );
};

export default SKill;

const SkillDiv = styled.div`
  height: 600px;
  text-align: center;
`;

const Heading = styled.h1`
  color: #149eca;
  font-size: 40px;
  font-family: "Courier New", Courier, monospace;
  text-decoration: underline;
`;

const ImageDiv = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 200px);
  /* grid-template-rows: repeat(3, 200px); */
  grid-gap: 20px;
  justify-content: center;
  margin-left: 100px;
`;

const Image = styled.img`
  width: 100px;
  height: 100px;
  margin-top: 15px;
`;
